@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "DM Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #313131;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  margin-top: 0;
  width: 90%;
  max-width: 960px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.Responsive-footer {
  display: none;
}

.Responsive-navigation {
  display: none;
}

@media all and (max-width: 700px) {
  .Responsive-footer {
    order: 3;
    display: block;
    width: 100%;
  }
}

article {
  flex-grow: 2;
  order: 2;
  padding-top: 180px;
  z-index: 1;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.Greeting-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 170px;
}

.Mugshot {
  border-radius: 100%;
  width: 150px;
}

.Hello {
  margin: 15px 0;
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 600;
}

.Description {
  margin: 15px 0;
  font-size: 2rem;
  font-weight: 400;
}

@media all and (max-width: 700px) {
  article {
    order: 1;
    padding-top: 120px;
  }
  article .Footer-wrapper {
    display: none;
  }

  .Greeting-wrapper {
    position: relative;
    width: 100%;
    top: 0;
  }

  .Mugshot {
    width: 100px;
  }

  .Hello {
    font-size: 5rem;
    line-height: 5.5rem;
    margin-top: 15px;
    margin-bottom: 0;
  }

  .Description {
    font-size: 2em;
    margin-top: 15px;
    margin-bottom: 0;
  }
}

.Footer-wrapper {
  width: 90%;
  max-width: 960px;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}

.Socials {
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Copyright {
  text-align: right;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.6);
}

svg {
  fill: rgba(255, 255, 255, 0.6);
}

svg:hover {
  fill: white;
}

@media all and (max-width: 700px) {
  .Footer-wrapper {
    align-items: center;
    margin: 50px auto 50px auto;
    position: relative;
  }
}

.ProjectSection {
  width: 50%;
  flex-grow: 2;
  order: 2;
  padding-top: 180px;
  z-index: 1;
  width: 50%;
}

.ProjSec-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.Project-image {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-top: 20px;
}

.Project-title {
  font-size: 3rem;
}

.Project-description {
  font-size: 1.5rem;
}

@media all and (max-width: 700px) {
  .ProjectSection {
    order: 1;
    padding-top: 120px;
  }
  article .Footer {
    display: none;
  }
}

.App-header {
  background-color: rgba(49, 49, 49, 0.8);
  width: 100%;
  height: 170px;
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  display: flex;
  position: fixed;
  top: 0;
  align-items: center;
  z-index: 2;
}

.Header-contents {
  display: flex;
  width: 90%;
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.Logo {
  width: 150px;
  fill: white;
}

@media all and (max-width: 700px) {
  .App-header {
    height: 120px;
    z-index: 2;
    order: 2;
  }
  .Header-contents {
    justify-content: center;
  }
  .Logo {
    width: 130px;
  }
}

.Title-nav {
  display: none;
}

nav {
  padding-top: 180px;
  order: 1;
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  z-index: 0;
  width: 40%;
  margin-right: 50px;
}

.Project {
  border-radius: 10px;
  padding: 10px;
  color: black;
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: flex-end;
}

.Project::before {
  content: "";
  padding-bottom: 100%;
  display: inline-block;
  vertical-align: top;
}

.Project:hover {
  box-shadow: 8px 8px 20px 0px rgba(0, 0, 0, 0.4);
  transform: translate3d(0, -2px, 0);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.Category {
  margin: 0;
  font-weight: 400;
  font-size: 1rem;
}

.Title-project {
  margin: 0;
  font-weight: 800;
  font-size: 1.5rem;
}

.Project-1 {
  background-size: cover;
  background-image: linear-gradient(#ffbaa5, #ffbaa5),
    url(/Portfolio-Website/static/media/moticon_3d_shoe.ae7f2280.png);
  background-blend-mode: multiply;
}

.Project-2 {
  background-size: cover;
  background-image: linear-gradient(#ffd49b, #ffd49b), url(/Portfolio-Website/static/media/sardinha.a83484d0.png);
  background-blend-mode: multiply;
  background-color: #ffd49b;
}

.Project-3 {
  background-color: #eaefb3;
}
.Project-4 {
  background-color: #8fe4ed;
}
.Project-5 {
  background-color: #daa8dc;
}
.Project-6 {
  background-color: #a6d795;
}
.Project-7 {
  background-color: #8bc8c4;
}

@media all and (max-width: 700px) {
  nav {
    padding-top: 0;
    width: 100%;
    margin-right: auto;
    order: 2;
  }

  .Title-nav {
    grid-column: 1/3;
    display: block;
    font-size: 2.5rem;
  }
}

